@charset "utf-8";

.section-intro {
    padding-bottom: 40px;
    background: #f2f2f2;

    .sub-header {
        margin-bottom: 40px;
        background-image: url(../images/bg_intro.jpg);
    }

    .intro-wrap {
        position: relative;
        padding-left: 200px;
    }

    // 공지사항
    .notice-wrap {
        padding-top: 15px;

        h3.title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }

        .search-board {
            margin-bottom: 30px;
        }

        .notice-button {
            margin-top: 10px;
            text-align: right;
    
            .btn-medium {
                min-width: 90px;
            }
        }
    
        .paging {
            margin-top: 10px;
        }

        .board-input {
            border-top: 2px solid #606b7f;

            th {
                text-align: center;
            }

            .textarea {
                display: block;
                width: 100%;
                height: 420px;
                margin: 10px 0;
                padding: 20px;
                border: 1px solid #e2e2e2;
                background-color: #fff;

                @include box-sizing;
                @include fontSize(16px);
            }
        }
    }

    // 문의하기
    .qna-wrap {
        padding-top: 15px;

        h3.title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }

        .board-input {
            border-top: 2px solid #606b7f;

            th {
                text-align: center;
            }

            .textarea {
                display: block;
                width: 100%;
                height: 420px;
                margin: 10px 0;
                padding: 20px;
                border: 1px solid #e2e2e2;
                background-color: #fff;

                @include box-sizing;
                @include fontSize(16px);
            }
        }

        .qna-button {
            margin-top: 10px;
            text-align: center;
        }
    }

    // 문의
    .help-guide {
        margin-top: 190px;
    }
}